@font-face {
  font-family: 'Satoshi-Medium';
  src: url('./fonts/Satoshi-Medium.woff2') format('woff2');
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@tailwind base;
@tailwind components;
@tailwind utilities;


.bg-about {
  background: -webkit-linear-gradient(to bottom, #000, #312e81);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #000, #312e81);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.bg-experience {
  background: -webkit-linear-gradient(to bottom, #312e81, #6b21a8);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #312e81, #6b21a8);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.bg-skill {
  background: -webkit-linear-gradient(to bottom, #6b21a8, #000);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #6b21a8, #000);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

body {
  margin: 0;
  font-family: 'Satoshi-Medium';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}